/* Start of button code */
.button-82-pushable {
  position: relative;
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
  outline-offset: 4px;
  transition: -webkit-filter 150ms;
  transition: filter 150ms;
  transition: filter 150ms, -webkit-filter 150ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  /* Need below code to center the button */
  margin: 0 auto;
  display: block;
  /* Set margins */
  margin-top: 10px;
  margin-bottom: 10px;
}
  
.button-82-shadow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  background: hsl(0deg 0% 0% / 0.25);
  will-change: transform;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
  transition:
    -webkit-transform
    600ms
    cubic-bezier(.3, .7, .4, 1);
  transition:
    transform
    600ms
    cubic-bezier(.3, .7, .4, 1);
  transition:
    transform
    600ms
    cubic-bezier(.3, .7, .4, 1), -webkit-transform
    600ms
    cubic-bezier(.3, .7, .4, 1);
}
  
.button-82-edge {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  background: linear-gradient(
    to left,
    hsl(340deg 100% 16%) 0%,
    hsl(340deg 100% 32%) 8%,
    hsl(340deg 100% 32%) 92%,
    hsl(340deg 100% 16%) 100%
  );
}
  
.button-82-front {
  display: block;
  position: relative;
  padding: 12px 27px;
  border-radius: 12px;
  font-size: 1.1rem;
  color: white;
  background: hsl(345deg 100% 47%);
  will-change: transform;
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
  transition:
    -webkit-transform
    600ms
    cubic-bezier(.3, .7, .4, 1);
  transition:
    transform
    600ms
    cubic-bezier(.3, .7, .4, 1);
  transition:
    transform
    600ms
    cubic-bezier(.3, .7, .4, 1), -webkit-transform
    600ms
    cubic-bezier(.3, .7, .4, 1);
}

@media (min-width: 768px) {
  .button-82-front {
    font-size: 1.25rem;
    padding: 12px 42px;
  }
}

.button-82-pushable:hover {
  filter: brightness(110%);
  -webkit-filter: brightness(110%);
}

.button-82-pushable:hover .button-82-front {
  -webkit-transform: translateY(-6px);
          transform: translateY(-6px);
  transition:
    -webkit-transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5);
  transition:
    transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5);
  transition:
    transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5), -webkit-transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5);
}

.button-82-pushable:active .button-82-front {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  transition: -webkit-transform 34ms;
  transition: transform 34ms;
  transition: transform 34ms, -webkit-transform 34ms;
}

.button-82-pushable:hover .button-82-shadow {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  transition:
    -webkit-transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5);
  transition:
    transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5);
  transition:
    transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5), -webkit-transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5);
}

.button-82-pushable:active .button-82-shadow {
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  transition: -webkit-transform 34ms;
  transition: transform 34ms;
  transition: transform 34ms, -webkit-transform 34ms;
}

.button-82-pushable:focus:not(:focus-visible) {
  outline: none;
}
/* End of button code */

